.mobile-btn {
  /* TODO be better with z-index */
  z-index: 99999999;
  position: fixed;
  bottom: 15px;
  right: 10px;
  border: none;
  height: 50px;
  width: 50px;
  border-radius: 100%;
  background-color: #023670;
  -webkit-box-shadow: 2px 2px 10px 1px black;
  box-shadow:         2px 2px 10px 1px black;
}

.mobile-btn:focus {
  outline: 0;
}

.mobile-btn-icon {
  position: relative;
  width: 20px;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}



